@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.cdnfonts.com/css/poppins');

* {
    margin: 0;
    padding: 0;
    font-family: Poppins, Ubuntu, sans-serif;
    scroll-behavior: smooth;
}
html,
body {
    width: 100%;
    height: 100%;
}

#root {
    width: 100%;
    height: 100%;
}
.bg-gradient {
    background-image: linear-gradient(to left, #a90a47, 80%, #c73f73);
}
.App {
    background-image: linear-gradient(
        to bottom,
        #1c6899 50%,
        #1c6899,
        #900c3f 50%
    );
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
        'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
        'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}
.react-calendar {
    --tw-bg-opacity: 1;
    position: relative;
    font-family: Poppins, Ubuntu, sans-serif;
    line-height: 1.125em;
    color: rgb(150 150 150 / var(--tw-text-opacity));
    border: 0 !important;
    padding: 3.3px 1.25rem;
}

.react-calendar__month-view__days {
    background-color: white;
}

.react-calendar__navigation__next2-button,
.react-calendar__navigation__prev2-button {
    display: none;
}

button,
input,
optgroup,
select,
textarea {
    font-family: inherit;
    font-size: 100%;
    font-weight: inherit;
    line-height: inherit;
    color: inherit;
    margin: 0;
    padding: 0;
}

.react-calendar__navigation button,
.react-calendar__navigation button:disabled {
    color: #fff;
    background: none;
    height: 20px;
    font-weight: 600;
    justify-content: space-between;
    background-color: transparent !important;
}

.react-calendar__month-view__weekdays {
    margin-bottom: 20px;
    padding-left: 10px;
    margin-top: 14px;
    color: #fff;
}

.react-calendar::before {
    --tw-bg-opacity: 1;
    background-color: rgb(220 0 68 / var(--tw-bg-opacity));
    content: ' ';
    display: block;
    position: absolute;
    width: 100%;
    height: 64px;
    top: 0;
    left: 0;
}

.react-calendar__navigation {
    z-index: 1;
    position: relative;
    font-size: 15px;
    height: 15px !important;
    color: white;
}

.react-calendar__month-view__weekdays {
    color: white !important;
    text-transform: none;
    text-decoration: none !important;
    margin: 0;
    margin-bottom: 10px;
}

.react-calendar__month-view {
    font-family: Poppins, Ubuntu, sans-serif;
}

.react-calendar__month-view__days,
.react-calendar__month-view__weekdays {
    z-index: 1;
    position: relative;
}

.react-calendar__month-view__weekdays__weekday {
    text-decoration: none !important;
}

.react-calendar abbr {
    text-decoration: none;
    text-transform: lowercase;
    font-weight: normal;
}

.react-calendar__tile,
.react-calendar__month-view__days__day--weekend {
    aspect-ratio: 1 / 1;
    border-radius: 100%;
    font-size: 13px;
    border: transparent 2px solid;
    --tw-text-opacity: 1;
    color: rgb(150 150 150 / var(--tw-text-opacity)) !important;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
}

.react-calendar__tile abbr {
    font-weight: 600 !important;
}

.react-calendar__tile.available {
    --tw-bg-opacity: 1;
    --tw-text-opacity: 1;
    background-color: rgb(68 175 105 / var(--tw-bg-opacity));
    color: rgb(255 255 255 / var(--tw-text-opacity)) !important;
}

.available {
    --tw-bg-opacity: 1;
    --tw-text-opacity: 1;
    background-color: rgb(68 175 105 / var(--tw-bg-opacity));
    color: rgb(255 255 255 / var(--tw-text-opacity)) !important;
}

.react-calendar__month-view__days {
    display: grid !important;
    grid-template-columns: repeat(7, 1fr) !important;
    grid-template-rows: repeat(5, 1fr) !important;
    grid-column-gap: 2px !important;
    grid-row-gap: 2px !important;
}

.react-calendar__tile--now {
    box-sizing: border-box !important;
    border: 2px solid black !important;
    --tw-border-opacity: 1;
    --tw-bg-opacity: 1;
    color: white !important;
    background-color: rgb(68 175 105 / var(--tw-bg-opacity)) !important;
    /* border-color: rgb(37 35 50 / var(--tw-border-opacity)) !important; */
}

.react-calendar__tile--active {
    background: #1c6899 !important;
    color: white;
}

.react-calendar__tile:disabled {
    background-color: #fff !important;
}
